#blogs-heading{
    font-size: 36px;
}
#featured-blogs-row-container{
    width: 80vw;
    margin: 10px 0px;
}
#featured-blog-title{
    font-size: 24px;
    color: #005985;
}
#featured-blogs-row-container:hover #featured-blog-title{
    color: #007fbe; 
    
}
#featured-blog-description{
    width: 80vw;
    font-size: 18px;
}
@media screen and (max-width: 768px){
    #blogs-heading{
        font-size: 28px;
    }
    #featured-blogs-row-container{
        width: 90vw;
    }
    #featured-blog-title{
        font-size: 20px;
    }
    #featured-blog-description{
        width: 85vw;
        font-size: 14px;
    }
}